header{
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden; 
}

.header__container{
    text-align: center;
    height: 100%;
    position: relative;
}

.cta{
    margin-top: 2.5rem;
    display: flex;
    gap: 2rem;
    justify-content: center;
}

.header__socials{
    display: flex;
    flex-direction: column;
    align-content: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header__socials::after{
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}
